.popup {
  width: 100%;
  height: 100%;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility .6s, opacity .6s ease;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  text-rendering: optimizeLegibility;
}
